import React, { memo } from 'react';

import './Tabs.scss';
import { NavLink, Outlet } from 'react-router-dom';
import cn from 'classnames';
import { tabs as tabsFromServer } from '../../api/tabs';

export const Tabs: React.FC = memo(() => {
  const tabs = tabsFromServer;

  return (
    <div className="tabs">
      <ul className="tabs__list">
        {tabs.map(tab => (
          <li
            key={tab.id}
            className="tabs__item"
          >
            <NavLink
              to={`/tabs/${tab.id}`}
              className={({ isActive }) => cn(
                'tabs__button',
                { 'tabs__button--active': isActive },
              )}
            >
              {tab.title}
            </NavLink>
          </li>
        ))}
      </ul>

      <Outlet />
    </div>
  );
});
